/* item space top */
.css_route_field .css_route_groups .css_route_group .css_route_group_tracks .css_route_group_items_track .css_route_group_item_space_top {
  position: relative;
  width: 100%;
  height: 95px;
  background: transparent;
}

/* item space bottom */
.css_route_field .css_route_groups .css_route_group .css_route_group_tracks .css_route_group_items_track .css_route_group_item_space_bottom {
  position: relative;
  width: 100%;
  height: 100vh;
  background: transparent;
  display: none;
}

.css_route_field .css_route_groups .css_route_group .css_route_group_tracks .css_route_group_items_track .css_route_group_item_space_bottom[displayed="true"] {
  display: block;
}

/* item */
.css_route_field .css_route_groups .css_route_group .css_route_group_tracks .css_route_group_items_track .css_route_group_item {
  position: relative;
  height: 50px;
  width: 100%;
  margin: 0px;
  padding: 0px;
  transition: none;
  display: block;
  background: var(--b-cssvar-ffffff-normal);
  overflow: clip;
  contain: layout;
}

.css_route_field .css_route_groups .css_route_group .css_route_group_tracks .css_route_group_items_track .css_route_group_item:last-child {
  margin-bottom: var(--b-cssvar-safe-area-bottom);
}

/* stretching: true */
.css_route_field .css_route_groups .css_route_group .css_route_group_tracks .css_route_group_items_track .css_route_group_item[stretching="true"] {
  position: absolute;
  /* left: var(--b-cssvar-css-route-group-item-x); p*/
  left: 0px;
  top: var(--b-cssvar-css-route-group-item-y);
  /* clip-path: inset(0 0 0 0); */
}

/* push-direction: 1 && push-state: 0 */
.css_route_field .css_route_groups .css_route_group .css_route_group_tracks .css_route_group_items_track .css_route_group_item[push-direction="1"][push-state="0"] {
  transform: translateY(0px);
}

/* push-direction: 1 && push-state: 1 */
.css_route_field .css_route_groups .css_route_group .css_route_group_tracks .css_route_group_items_track .css_route_group_item[push-direction="1"][push-state="1"] {
  transform: translateY(50px);
  /* compensate the height of the already collapsed element separated from the document flow */
}

/* push-direction: 1 && push-state: 2 */
.css_route_field .css_route_groups .css_route_group .css_route_group_tracks .css_route_group_items_track .css_route_group_item[push-direction="1"][push-state="2"] {
  transform: translateY(calc(50px + 171px));
  transition: transform var(--b-cssvar-transition-duration);
  transition-timing-function: ease;
}

/* push-direction: 2 && push-state: 0 */
.css_route_field .css_route_groups .css_route_group .css_route_group_tracks .css_route_group_items_track .css_route_group_item[push-direction="2"][push-state="0"] {
